export default "import React from 'react';\nimport {makeData} from '../data/Utils';\n// Import React Table\nimport ReactTable from 'react-table';\n\nclass CustomExpanderPosition extends React.Component {\n  constructor() {\n    super();\n    this.state = {\n      data: makeData(),\n    };\n  }\n\n  render() {\n    const {data} = this.state;\n    return (\n      <ReactTable\n        data={data}\n        columns={[\n          {\n            Header: 'Name',\n            columns: [\n              {\n                Header: 'First Name',\n                accessor: 'firstName',\n                Footer: () => (\n                  <div style={{textAlign: 'center'}}>First Name</div>\n                ),\n              },\n              {\n                Header: 'Last Name',\n                accessor: 'lastName',\n                Footer: () => (\n                  <div style={{textAlign: 'center'}}>Last Name</div>\n                ),\n              },\n            ],\n          },\n          {\n            Header: 'Info',\n            columns: [\n              {\n                Header: 'Age',\n                accessor: 'age',\n                Footer: () => <div style={{textAlign: 'center'}}>Age</div>,\n              },\n            ],\n          },\n          {\n            Header: 'Expand',\n            columns: [\n              {\n                expander: true,\n                Header: () => <strong>More</strong>,\n                width: 65,\n                Expander: ({isExpanded, ...rest}) => (\n                  <div>\n                    {isExpanded ? <span>&#x2299;</span> : <span>&#x2295;</span>}\n                  </div>\n                ),\n                style: {\n                  cursor: 'pointer',\n                  fontSize: 25,\n                  padding: '0',\n                  textAlign: 'center',\n                  userSelect: 'none',\n                },\n                Footer: () => <span>&hearts;</span>,\n              },\n            ],\n          },\n        ]}\n        defaultPageSize={10}\n        className='-striped -highlight'\n        SubComponent={() => <div style={{padding: '10px'}}>Hello</div>}\n      />\n    );\n  }\n}\n\nexport default CustomExpanderPosition;\n";