export default "import React from 'react';\nimport {makeData} from '../data/Utils';\n// Import React Table\nimport ReactTable from 'react-table';\n\nclass CustomColumnWidths extends React.Component {\n  constructor() {\n    super();\n    this.state = {\n      data: makeData(),\n    };\n  }\n\n  render() {\n    const {data} = this.state;\n    return (\n      <ReactTable\n        data={data}\n        columns={[\n          {\n            Header: 'Name',\n            columns: [\n              {\n                Header: 'First Name',\n                accessor: 'firstName',\n                maxWidth: 200,\n              },\n              {\n                Header: 'Last Name',\n                id: 'lastName',\n                accessor: (d) => d.lastName,\n                width: 300,\n              },\n            ],\n          },\n          {\n            Header: 'Info',\n            columns: [\n              {\n                Header: 'Age',\n                accessor: 'age',\n                minWidth: 400,\n              },\n            ],\n          },\n        ]}\n        defaultPageSize={10}\n        className='-striped -highlight'\n      />\n    );\n  }\n}\n\nexport default CustomColumnWidths;\n";